'use strict';

var parsePhrase = require('./parse-phrase');

module.exports = function getDictionarySubset(dictionary, maxLength, exclude) {

  return dictionary.filter(function (phrase) {

    var words = parsePhrase(phrase);
    var fits = words.length <= maxLength;
    var excludeWords = undefined;

    if (exclude) {
      excludeWords = parsePhrase(exclude);
    }

    var excluded = exclude && (phrase === exclude || words[0] === excludeWords[excludeWords.length - 1]);

    return fits && !excluded;
  });
};